"use client"
import * as Vader from "vaderjs-native"
import { useState, useArray } from "vaderjs-native"


function Counter() {
  const [count, setCount] = useState(0)
  
  return (  
    <div class="p-4">
      <h1 class="text-3xl font-bold underline text-center mb-4">
        Counter: {count}
      </h1>
      <div class="flex justify-center">
        <button class="bg-blue-300 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mr-2" onClick={() => setCount(count + 1)}>
          Increment
        </button>
        <button class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded" onClick={() => setCount(count - 1)}>
          Decrement
        </button>
        <p>HMR WORKS</p>
      </div>
    </div>
  )
}

Vader.render(<Counter />, document.getElementById("app")!)